<template>
  <div class="cart-item-box">
    <div class="cart-item" v-for="item in list" :key="item.id">
      <div class="cart-item-checkbox">
        <input type="checkbox" :checked="item.checked" @click="doCheck(item.id,$event)">
      </div>
      <img class="cart-item-img"
           :src="item.image" alt="">
      <div class="cart-item-content">
        <div class="cart-item-title">{{ item.title }}</div>
        <div class="cart-item-description">{{ item.desc }}</div>
        <div class="cart-item-info">
          <div class="cart-item-price">￥{{ item.price }}</div>
          <div>X {{ item.count }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CartItem",
  data() {
    return {
      list: []
    }
  },
  activated() {
    this.list = this.$store.state.cartList
  },
  methods:{
    doCheck(id,e){
      this.$store.commit('doChangeCheckBox',{id:id,checked:e.target.checked})
      this.$emit('doCheckAll')
    },
    doCheckAll(){
      this.list =  this.$store.state.cartList
    }
  }
}
</script>

