<template>
  <div class="cart-box">
    <NavBarComponent class="cart-nav-bar">
      <div slot="center">购物车（{{ count }}）</div>
    </NavBarComponent>
    <CartItem ref="cartItem" @doCheckAll="doCheckAll"></CartItem>
    <div class="cart-foot-bar">
      <div class="cart-foot-bar-check">
        <input class="cart-input" type="checkbox" id="checkAll" :checked="checked" @click="doCheckAlls($event)">
        <label class="cart-input-label" for="checkAll">全选</label>
      </div>
      <div>
        合计：{{price}}.00
      </div>
      <div class="cart-foot-bar-count">
        去计算({{count}})
      </div>
    </div>
  </div>
</template>

<script>
import NavBarComponent from '@/components/common/navBar/NavBarComponent.vue'
import CartItem from "@/views/cart/children/CartItem";

export default {
  name: "Cart",
  data() {
    return {
      count: 0,
      price:0,
      checked:false
    }
  },
  components: {
    NavBarComponent,
    CartItem
  },
  beforeUpdate() {
    console.log('cart update')
  },

  activated() {
    this.count = this.$store.state.cartListCount
    let price = 0;
    let check = true
    for (let i= 0;i< this.$store.state.cartList.length;i++){
      price+=this.$store.state.cartList[i].price*this.$store.state.cartList[i].count
      if (this.$store.state.cartList[i].checked===false){
        check = false
      }
    }
    this.checked = check
    this.price = price
  },
  methods:{
    doCheckAlls(e){
      this.$store.commit('doCheckAll',e.target.checked)
      this.$refs.cartItem.doCheckAll()
    },
    doCheckAll(){
      for (let i= 0;i< this.$store.state.cartList.length;i++){
        if (this.$store.state.cartList[i].checked===false){
          this.checked = false
          return
        }
      }
      this.checked = true
    }
  }
}
</script>

